.projects {
	width: 100%;
	box-sizing: border-box;
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	column-gap: 23px;
	row-gap: 23px;
	justify-content: center;
	padding-bottom: 1rem;
}

.project-image {
	display: flex;
	justify-content: center;
	overflow: hidden;
	position: relative;
	background-color: black;
	opacity: 1;
}

.project-image img {
	width: 100%;
	height: auto;
}

.project-image-label {
	position: absolute;
	top: 40px;
	left: 20px;
	transform: translateY(-50%);
	color: white;
	font-size: 36px;
	font-weight: 400;
	text-transform: uppercase;
	/* width: 100%; */
	height: 40px;
	display: flex;
	justify-content: center;
	align-items: center;

	opacity: 0;
	transition: opacity 0.3s;
}

.image-shadow {
	position: absolute;
	width: 100%;
	height: 100%;
	background-color: rgba(45, 45, 45, 0.935);
	/* display: none; */
	opacity: 0;
	transition: opacity 0.5s;
}

.project-image:hover {
	opacity: 0.92;
	cursor: pointer;
}

.project-image:hover .image-shadow {
	/* display: flex; */
	opacity: 1;
}

.project-image:hover .project-image-label {
	opacity: 1;
}

@media only screen and (max-width: 1450px) {
	.projects {
		grid-template-columns: 1fr 1fr;
		row-gap: 15px;
	}
}

@media only screen and (max-width: 900px) {
	.projects {
		grid-template-columns: 1fr;
		row-gap: 15px;
	}
}

@media only screen and (max-width: 1200px) {
	.projects {
		min-width: 0px;
	}
}
