.contact {
	width: 100%;
	max-width: 1200px;
}

.contact-container {
	width: 100%;
	display: flex;
	flex-direction: column;
}

.contacct-container > div {
	width: 100%;
}
.contact-image {
	width: 100%;
	margin-bottom: 50px;
}
.contact-image img {
	width: 100%;
	height: auto;
}

.contact-content {
	display: flex;
	flex-direction: row;
	margin-bottom: 50px;
}

.c-left {
	flex: 2;
}

.c-right {
	flex: 1;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.avatar-image {
	width: 150px;
	height: 150px;
	background-color: black;
	border-radius: 50% 50%;
	overflow: hidden;
	display: flex;
	justify-content: center;
	align-items: center;
}

.avatar-image img {
	width: auto;
	height: 100%;
}
