header {
	width: 100%;
	min-height: 90px;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	/* margin-bottom: 20px; */
	box-sizing: border-box;
	position: sticky;
	top: 0;
	left: 0;
	z-index: 10;
	background-color: rgba(255, 255, 255, 0.982);
	/* padding-top: 20px; */
	/* padding-bottom: 20px; */
}

.main-nav {
	display: flex;
	justify-content: center;
	align-items: center;
	margin: 0;
	padding: 0;
	color: rgb(61, 61, 61);
	font-weight: 300;
	/* margin-top: 10px; */
}

.main-nav li {
	margin-left: 45px;
	margin-right: 45px;
	font-size: 17px;

	text-transform: uppercase;
}

.current-li {
	color: black;
	font-weight: 400;
	border-bottom: solid 1px black;
}

.main-nav li:hover {
	cursor: pointer;
	color: black;
}

.logo {
	font-size: 18px;
	line-height: 26px;
	font-weight: 200;
	letter-spacing: -2px;
	color: rgb(0, 0, 0);
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	text-transform: uppercase;
}

.logo:hover {
	cursor: pointer;
}

.logo > div {
	display: flex;
	flex-direction: row;
	min-width: 87px;
	justify-content: space-between;
	align-items: flex-end;
}

.logo img {
	height: 50px;
}

.logo > span {
	margin: 0px 2px;
	font-size: 70px;
	line-height: 40px;
	font-weight: 900;
}

.hamburger {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	height: 24px;
	display: none;
	align-items: center;
	width: 70px;
}

.hamburger > div {
	width: 100%;
	height: 3px;
	background-color: black;
}

.hamburger > div:nth-child(2) {
	width: 60%;
	height: 2px;
}

.hamburger:hover {
	cursor: pointer;
	opacity: 0.8;
}

@media only screen and (max-width: 1400px) {
	.main-nav li {
		/* display: none; */
		font-size: 22px;
		margin-left: 30px;
		margin-right: 30px;
	}
}

@media only screen and (max-width: 940px) {
	.main-nav {
		display: none;
	}
	.hamburger {
		display: flex;
	}
	header {
		flex-direction: row;
	}
}

@media only screen and (max-width: 640px) {
	.logo {
		font-size: 42px;
	}
	.hamburger {
		width: 60px;
		height: 25px;
	}
}

@media only screen and (max-width: 500px) {
	.logo {
		font-size: 35px;
	}
	.hamburger {
		width: 50px;
		height: 20px;
	}

	.App {
		padding: 0px 10px;
	}
}

@media only screen and (max-width: 360px) {
	.logo {
		font-size: 31px;
	}
	.hamburger {
		width: 40px;
		height: 20px;
	}

	.App {
		padding: 0px 10px;
	}
}
