section {
	min-height: 300px;
	z-index: 2;
	position: relative;
	display: flex;
	flex-direction: column;
	align-items: center;
	width: 100%;
	margin-top: 40px;
}

ul,
li,
a {
	text-decoration: none;
	list-style: none;
	color: inherit;
}

html,
body,
div {
	font-family: 'Roboto';
}

.App {
	position: relative;
	width: 100%;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 0px 35px;
	box-sizing: border-box;
}
p {
	font-size: 16px;
	line-height: 26px;
	font-weight: 300;
	color: black;
	letter-spacing: 0.2px;
}

h1 {
	font-size: 38px;
	font-weight: 800;
	letter-spacing: -2px;
	text-transform: capitalize;
}

h1,
h2,
h3 {
	margin: 0;
	padding: 0;
}
